// mplement leaflet.js 

mapboxgl.accessToken = 'pk.eyJ1IjoiZGVyZ3V0ZSIsImEiOiJja3pheWczajgwOXd6MndxaXFmY3k2bGxlIn0.Nrz0K7zjqy9pYRtuBhZ3Zg';
const map = new mapboxgl.Map({
    container: 'map', // container ID
    style: 'mapbox://styles/dergute/ckzb3oib4003p14tai2n4pt1n', // style URL
    center: [-71.32, 41.49], // starting position [lng, lat]
    zoom: 13.7 // starting zoom
});

map.on('click', (event) => {
    // If the user clicked on one of your markers, get its information.
    const features = map.queryRenderedFeatures(event.point, {
        layers: ['art-gallery'] // replace with your layer name
    });
    if (!features.length) {
        return;
    }
    const feature = features[0];

    /* 
    Create a popup, specify its options 
    and properties, and add it to the map.
  */
    const popup = new mapboxgl.Popup({ offset: [0, -15] })
        .setLngLat(feature.geometry.coordinates)
        .setHTML(
            `<h3>${feature.properties.title}</h3><p>${feature.properties.description}</p>`
        )
        .addTo(map);
});
